import React from 'react';
import Input from "antd/es/input";
import Button from "antd/es/button";
import IconButton from "../../assets/icons/IconButton";
import Upload from "antd/es/upload";
import {message} from "antd";

const Download = ({setStep, setSkipForm, files, setFiles}) => {
	const props = {
		name: 'file',
		action: '/ajax/send/save.php',
		headers: {
			authorization: 'authorization-text',
		},
		onChange(info) {
			if (info.file.status !== 'uploading') {

			}
			if (info.file.status === 'done') {
				message.success(`${info.file.name} file uploaded successfully`);
				setFiles([...files, { name: info.file.name, url: info.file.response }])
			} else if (info.file.status === 'removed') {
				setFiles([...files].filter((el) => el.name !== info.file.name))
			} else if (info.file.status === 'error') {
				message.error(`${info.file.name} file upload failed.`);
			}
		}
	};

	return (
		<div className="cp-download-form">
			<div className="cp-row">
				<div className="cp-title"> </div>
				<div className="cp-text">          </div>
				<div className="cp-line">
					<Input placeholder='  '/>
				</div>
				<div className="cp-dl-wrap">
					<Upload {...props}>
						<Button className="with-arrow" type="primary"> <IconButton/></Button>
					</Upload>
				</div>
				<div className="cp-actions">
					<div className="btn-white" onClick={() => {
						setSkipForm();
						setStep(0)
					}}><span className="mobHide">&nbsp;</span><span></span></div>
					<Button className="with-arrow" type="primary" onClick={() => setStep(2)}><IconButton/></Button>
				</div>
			</div>
		</div>
	);
};

export default Download;
